import React from 'react';

import styles from './SortPopup.module.scss'

function SortPopup({ items, setSort }) {
  const [SortPopup, setSortPopup] = React.useState(false);
  const [activeItem, setActiveItem] = React.useState(0);
  const sortRef = React.useRef();

  const toggleVisiblePopup = () => {
    SortPopup ? setSortPopup(false) : setSortPopup(true);
  };

  const handleOutSideClick = (e) => {
    if (!e.path.includes(sortRef.current)) {
      setSortPopup(false);
    }
  };

  React.useEffect(() => {
    document.body.addEventListener('click', handleOutSideClick);
  }, []);

  const onSelectItem = (index) => {
    setActiveItem(index);
    setSortPopup(false);
  };
  React.useEffect(() => {
    setSort(items[activeItem].type);
  }, [activeItem]);

  const activeLable = items[activeItem].name;

  return (
    <div className={styles.sort} ref={sortRef}>
      <div className={styles.label}>
        <svg
          className={SortPopup ? styles.rotated : ''}
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="#f3f3f3"
          xmlns="http://www.w3.org/2000/svg">
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
          />
        </svg>
        <b>Sort by:</b>
        <span onClick={toggleVisiblePopup}>{activeLable}</span>
      </div>
      {SortPopup && (
        <div className={styles.popup}>
          <ul>
            {items.map((items, index) => (
              <li
                className={activeItem === index ? 'active' : ''}
                onClick={() => onSelectItem(index)}
                key={(items, index)}>
                {items.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
}

export default SortPopup;
